/* Styles for Product Finder component (prefixed file) */
.container {
  max-width: 900px;
  margin: 36px auto;
  padding: 24px;
  background: #ffe6d5fd;
  border-radius: 10px;
  box-shadow: 0 6px 20px rgba(16,24,40,0.06);
  font-family: Inter, ui-sans-serif, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
  color: #0f172a;
}

.container h1 {
  font-size: 50px;
  font-weight: 700;
  letter-spacing: -0.5px;
  margin-bottom: 25px;
}


.progress-wrapper {
  height: 10px;
  background: #eef2ff;
  border-radius: 999px;
  overflow: hidden;
  margin: 12px 0 18px 0;
}

.progress-bar {
  height: 100%;
  background: linear-gradient(90deg, #fd6602 0%, #e39127 100%);
  transition: width 280ms ease;
}

.option-card {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid #e6eef7;
  margin: 8px 0;
  cursor: pointer;
  user-select: none;
  background: #ffffff;
}

.option-card input {
  margin: 0;
  width: 18px;
  height: 18px;
  accent-color: #f09c57;
}

.option-card.selected {
  background: #f5f3ff;
  border-color: #ff5100;
  box-shadow: 0 4px 14px rgba(79,70,229,0.06);
}

.buttons {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  margin-top: 20px;
}

button {
  padding: 10px 18px;
  border: none;
  border-radius: 8px;
  background: linear-gradient(135deg, #ff8c42, #ff6b00);
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s ease;
}

button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgb(255, 129, 39);
}

.error {
  color: #dc2626;
  margin-top: 8px;
}

.plan-card {
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 15px;
  color: white;
}

/* Starter - Green */
.plan-card.starter {
  background: linear-gradient(135deg, #004bed, #0033bf);
}

/* Professional - Orange */
.plan-card.professional {
  background: linear-gradient(135deg, #ff8a00, #ff6b00);
}

/* Enterprise - Violet */
.plan-card.enterprise {
  background: linear-gradient(135deg, #9b59b6, #6c3483);
}

/* Highlight animation */
.plan-card.highlight {
  transform: scale(1.05);
  box-shadow: 0 15px 30px rgb(255, 85, 0);
}

.modal-overlay {
  position: fixed;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(2,6,23,0.5);
  z-index: 60;
}

.modal {
  width: 92%;
  max-width: 480px;
  background: #f7ddcb;
  border-radius: 10px;
  padding: 18px;
  box-shadow: 0 12px 40px rgba(2,6,23,0.2);
}

.modal input {
  width: 100%;
  padding: 4.5px;
  margin: 8px 0;
  border-radius: 8px;
  border: 1px solid #e6eef7;
}

ul { margin-top: 8px; }

@media (max-width: 640px) {
  .container { margin: 20px; padding: 16px; }
  .buttons { justify-content: stretch; flex-direction: column-reverse; }
  button { width: 100%; }
}
/* RESULT LAYOUT FIX */

.result-container {
  max-width: 900px;
}

.plans-grid {
  display: flex;
  gap: 20px;
  margin: 20px 0;
}

.plan-card {
  flex: 1;
  padding: 25px;
  border-radius: 12px;
  color: white;
  text-align: center;
  min-height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Green */
.plan-card.starter {
  background: linear-gradient(135deg, #2ecc71, #27ae60);
}

/* Blue */
.plan-card.professional {
  background: linear-gradient(135deg, #3498db, #2980b9);
}

/* Violet */
.plan-card.enterprise {
  background: linear-gradient(135deg, #9b59b6, #6c3483);
}

.plan-card.highlight {
  transform: scale(1.09);
  box-shadow: 0 15px 30px rgba(0,0,0,0.25);
}

.breakdown-box {
  background: #ffffff;
  padding: 20px;
  border-radius: 12px;
  margin: 20px 0;
}

.score {
  font-weight: bold;
  margin-bottom: 10px;
}
/* PREMIUM STEP TRANSITION */

.step-wrapper {
  animation-duration: 0.45s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.22, 1, 0.36, 1);
}

.step-wrapper.forward {
  animation-name: slideForward;
}

.step-wrapper.backward {
  animation-name: slideBackward;
}

@keyframes slideForward {
  0% {
    opacity: 0;
    transform: translateX(60px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideBackward {
  0% {
    opacity: 0;
    transform: translateX(-60px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
