.navbar {
  font-family: 'Poppins', sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 40px;
  background: white;
  border-bottom: 1px solid #eee;
  position: sticky;
  top: 0;
  z-index: 1000;
  font-family: 'Poppins', sans-serif;  /* ðŸ‘ˆ Added */
}

.nav-left {
  display: flex;
  align-items: center;
  gap: 40px;
}

.logo {
  font-size: 24px;
  font-weight: 700; /* stronger */
  display: flex;
  align-items: center;
  gap: 8px;
}

.logo-icon {
  font-size: 22px;
  color: #ff6b00;
}

.orange {
  color: #ff6b00;
}

.nav-links a {
  text-decoration: none;
  color: #555;
  font-weight: 500;
  margin-right: 20px;
  letter-spacing: 0.3px; /* subtle premium feel */
  transition: 0.3s ease;
}

.nav-links a:hover {
  color: #ff6b00;
}

.nav-right {
  display: flex;
  align-items: center;
  gap: 20px;
}

.signin {
  text-decoration: none;
  color: #333;
  font-weight: 600;
}

.signup {
  text-decoration: none;
  background: #ff6b00;
  color: white;
  padding: 10px 18px;
  border-radius: 6px;
  font-weight: bold;
  transition: 0.3s ease;
}

.signup:hover {
  opacity: 0.85;
}